<template>
  <CChartPie :datasets="defaultDatasets" :labels="['1', '2', '3']" />
</template>

<script>
import { CChartPie } from '@coreui/vue-chartjs';

export default {
  name: 'CChartPieExample',
  components: { CChartPie },
  computed: {
    defaultDatasets() {
      return [
        {
          backgroundColor: ['#41B883', '#DD1B16', '#FFD306'],
          data: [30, 20, 70],
        },
      ];
    },
  },
};
</script>
