<template>
  <div>
    <!-- 卡片 -->
    <!-- <OverviewWidgets /> -->

    <CRow>
      <CCol sm="6" lg="5">
        <CWidgetDropdown header="$0.00" text="截至今日費用">
          <template #default>
            <CCol style="position: relative;">
              <!-- <CButton block variant="ghost" color="second" class="downloadButton">
                <CIcon name="cil-cloud-download" />
              </CButton> -->
              <CButton block variant="outline" color="dark" class="downloadButton">
                <CIcon name="cil-cloud-download" />
              </CButton>
            </CCol>
          </template>
          <template #footer>
            <div class="carDate">{{ costDate }}</div>
          </template>
        </CWidgetDropdown>
      </CCol>
    </CRow>

    <CRow>
      <CCol sm="12">
        <CCard>
          <CCardBody>
            <CRow>
              <CCol sm="5">
                <span class="titleFont ">{{ $t('billing.totalFolw') }}</span>
                <div class="small text-muted">{{ getMonth }}</div>
              </CCol>
              <CCol sm="7" class="d-none d-md-block">
                <CButtonGroup class="float-right mr-3">
                  <CButton
                    color="outline-secondary"
                    v-for="(value, key) in ['Day', 'Month']"
                    :key="key"
                    class="mx-0"
                    :pressed="value === selected ? true : false"
                    @click="
                      selected = value;
                      clickButton(value, 1);
                    "
                  >
                    {{ value }}
                  </CButton>
                  <!-- <CButton color="secondary" class="mx-0">Button</CButton>
                  <CButton color="outline-secondary" class="mx-0">Button</CButton> -->
                </CButtonGroup>
              </CCol>
            </CRow>
            <!-- <OverviewChart style="height:300px;margin-top:40px;" /> -->
            <OverviewChartFlow :msge="clickFlowButton" />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
    <CRow>
      <CCol sm="12">
        <CCard>
          <CCardBody>
            <CRow>
              <CCol sm="5">
                <span class="titleFont ">{{ $t('billing.totalCost') }}</span>
                <div class="small text-muted">{{ getMonth }}</div>
              </CCol>
              <CCol sm="7" class="d-none d-md-block">
                <CButtonGroup class="float-right mr-3">
                  <CButton
                    color="outline-secondary"
                    v-for="(val, i) in ['Day', 'Month']"
                    :key="i"
                    class="mx-0"
                    :pressed="val === selected1 ? true : false"
                    @click="
                      selected1 = val;
                      clickButton(val, 2);
                    "
                  >
                    {{ val }}
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
            <OverviewChartCost :msge="clickCostButton" />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
    <CRow>
      <!-- mobile -->
      <CCol sm="2" class="mobileSelect">
        <CCard>
          <CCardBody>
            <CSelect :label="$t('billing.default')" :options="option1" class="projectSelect" />
            <CSelect :label="$t('billing.timeLimit')" :options="option2" class="projectSelect" />
            <CSelect :label="$t('billing.groupBy')" :options="option3" class="projectSelect" />
          </CCardBody>
        </CCard>
      </CCol>
      <CCol sm="9">
        <CCard>
          <CCardBody>
            <!-- 專案 -->
            <CDataTable :items="project_items" :fields="project_fields" table-filter items-per-page-select :items-per-page="10" hover sorter pagination v-if="projectTable"> </CDataTable>
            <!-- 服務 column-filter-->
            <CDataTable :items="service_items" :fields="service_fields" table-filter items-per-page-select :items-per-page="10" hover sorter pagination v-if="serviceTable"> </CDataTable>
            <!-- SKU -->
            <CDataTable :items="sku_items" :fields="sku_fields" table-filter items-per-page-select :items-per-page="10" hover sorter pagination v-if="skuTable"> </CDataTable>
          </CCardBody>
        </CCard>
      </CCol>
      <!-- web -->
      <CCol sm="3" class="webSelect">
        <CCard>
          <CCardBody>
            <CSelect :label="$t('billing.default')" :options="option1" class="projectSelect" />
            <CSelect :label="$t('billing.timeLimit')" :options="option2" class="projectSelect" />
            <label for="uid-ytow23uoh9">{{ $t('billing.groupBy') }}</label>
            <select class="form-control" v-model="groupBy">
              <option value="1" selected="selected">{{ $t('billing.project') }}</option>
              <option value="2">{{ $t('billing.service') }}</option>
              <option value="3">SKU</option>
            </select>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  </div>
</template>

<script>
import OverviewChartFlow from '../charts/OverviewChartFlow';
import OverviewChartCost from '../charts/OverviewChartCost';
import OverviewWidgets from '../widgets/OverviewWidgets';
import WidgetsBrand from '../widgets/WidgetsBrand';
import Bus from '../../bus.js';

export default {
  name: 'Overview',
  components: {
    OverviewChartFlow,
    OverviewChartCost,
    OverviewWidgets,
    WidgetsBrand,
  },
  data() {
    return {
      selected: 'Month',
      selected1: 'Month',
      groupBy: 1,
      option: [1, 2],
      option1: ['本月所有專案', '本月所有服務', '上月所有專案', '上月所有服務'],
      option2: ['當前月份', '上個月', '最近30天', '自訂'],
      option3: ['專案', '服務', 'SKU'],
      project_items: [
        { project: 'projectTest', projectID: 'projectTest', projectNumber: '8516359843', cost: '$ 120.00' },
        { project: 'myProject', projectID: 'myProject', projectNumber: '6261291528', cost: '$ 0.05' },
      ],
      service_items: [
        { service: 'Cloud Storage', cost: '$ 120.00' },
        { service: 'Stackdriver Logging', cost: '$ 0.05' },
        { service: 'Compute Engine', cost: '$2,870.25' },
      ],
      sku_items: [
        { sku: 'Download APAC', service: 'Cloud Storage', skuID: '1F8B-71B0-3D1B', flow: '0 gibibyte', cost: '$0.00' },
        { sku: 'E2 Instance Core running in Americas', service: 'Compute Engine', skuID: 'CF4E-A0C7-E3BF', flow: '1,344 hour', cost: '$821.00' },
        { sku: 'External IP Charge on a Standard VM', service: 'Compute Engine', skuID: 'C054-7F72-A02E', flow: '2,016 hour', cost: '$150.56' },
        { sku: 'Licensing Fee for Debian 10 Buster (CPU cost)', service: 'Compute Engine', skuID: '0C5C-D8E4-38C1', flow: '2,016 hour', cost: '$0.00' },
        { sku: 'Log Volume', service: 'Stackdriver Logging', skuID: '143F-A1B0-E0BE', flow: '0 gibibyte', cost: '$0.00' },
      ],
      projectTable: true,
      serviceTable: false,
      skuTable: false,
      clickFlowButton: 'month',
      clickCostButton: 'month',
      getMonth: '',
      costDate: '',
    };
  },
  computed: {
    project_fields() {
      return [
        {
          key: 'project',
          label: this.$t('billing.project'),
        },
        {
          key: 'projectID',
          label: this.$t('billing.projectID'),
        },
        {
          key: 'projectNumber',
          label: this.$t('billing.projectNumber'),
        },
        {
          key: 'cost',
          label: this.$t('billing.cost'),
        },
      ];
    },
    service_fields() {
      return [
        {
          key: 'service',
          label: '服務',
        },
        {
          key: 'cost',
          label: '費用',
        },
      ];
    },
    sku_fields() {
      return [
        {
          key: 'sku',
          label: 'SKU',
        },
        {
          key: 'service',
          label: '服務',
        },
        {
          key: 'skuID',
          label: 'SKU ID',
        },
        {
          key: 'flow',
          label: '用量',
        },
        {
          key: 'cost',
          label: '費用',
        },
      ];
    },
  },

  watch: {
    groupBy: {
      handler(val) {
        // console.log('hi', val);
        if (val == '1') {
          this.projectTable = true;
          this.serviceTable = false;
          this.skuTable = false;
        } else if (val == '2') {
          this.projectTable = false;
          this.serviceTable = true;
          this.skuTable = false;
        } else if (val == '3') {
          this.projectTable = false;
          this.serviceTable = false;
          this.skuTable = true;
        }
      },
    },
    // clickFlowButton(val) {
    //   Bus.$emit('clickFlowButton', this.clickFlowButton);
    //   console.log('val', val, this.clickFlowButton);
    // },
    clickCostButton(val) {
      Bus.$emit('clickCostButton', this.clickCostButton);
    },
  },
  methods: {
    clickButton(val, type) {
      if (type == 1) {
        // console.log('val,type', val, type);
        this.clickFlowButton = val;
      } else if (type == 2) {
        // console.log('val,type', val, type);
        this.clickCostButton = val;
      }
    },
  },
  mounted() {
    // utils/common.js共用
    this.costDate = this.common.getDateRange();
    this.getMonth = this.common.getChartMonth();
  },
};
</script>
